.section-2 {
  background-color: var(--background-color-orange);
  width: auto;
  display: grid;
  grid-template-columns: 1fr;
  width: 100vw;
  font-family: "Poppins";
  padding-bottom: 51px;
}

.truck {
  width: 120px;
  margin: 46px auto auto auto;
}

.delivery {
  margin: 10px auto auto auto;
  text-align: center;
  color: white;
  font-size: var(--font-size-h1-h2);
  font-weight: 600;
}

.delivery-text {
  text-align: center;
  color: white;
  font-size: 14px;
  font-weight: normal;
}

.buy-button {
  margin: 5px auto auto auto;
  padding: 12px 24px;
  width: 127px;
  height: 24px;
  text-align: center;
  color: white;
  background-color: transparent;
  font-family: var(--font-size-text);
  font-size: var(--font-size-text);
}
